.data-container {
  background-color: rgb(216, 229, 230);
  border-radius: 11px;
  margin-top: 40px;
  padding-top: 40px;
  padding-bottom: 40px;
}
.calculate-btn {
  background-color: #3f51b5;
  padding: 15px 50px;
  color: white;
  font-size: 16px;
  border-radius: 44px;
  cursor: pointer;
  border: 1px solid #3f51b5;
  margin-bottom: 0px;
  margin-top: 20px;
  transform: translate3d(0, 0, 0);
  transition: all 0.2s ease;
}

.calculate-btn:hover {
  background-color: #fff;
  transform: translate(0px, -2px);
  color: #5364c3;
  box-shadow: 0px 15px 30px -12px rgba(255, 255, 255, 0.2);
}

.calculate-btn:focus {
  background-color: #32408f;
}

.calculate-btn:focus:hover {
  color: white;
}

.calculate-btn:disabled {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
  cursor: default;
}

.calculate-btn:disabled:hover {
  box-shadow: none;
  transform: translate(0, 0);
}

.warningrow{
  font-size: auto;
  color: red;

}
.info-container{
  margin-top: 10px;
}
.card {
  background-color:   #cccccc !important;
  color: rgb(15, 15, 15);
  font-weight: 800;

}
.card:hover{
  background-color:  #ebe0e0 !important
}
.card-title {
  font-weight: 100 !important;
  text-align: center;
}
.card-content{
  padding: 0px !important;
}

.card-data {
  display: flex;
  justify-content: space-around;
}

.delete-btn {
  background-color: #918d8d;
  color: white;
  border: none;
  border-radius: 50%;
  font-weight: 700;
  padding: 5px 9px;
  cursor: pointer;
  position: absolute;
  top: -12px;
  right: -12px;
}

.card-label{
  border-radius: 50%;
  padding: 2px 9px;
  position: absolute;
  top:-18px;
  left:0px;
  background-color: #cccccc ;
}
.card:hover .card-label{
  background-color:  #ebe0e0 !important
}
.card-line{
  border:none;
  border-top:10px dotted #cccccc;
 
  width: 10%;
  position: absolute;
  top:-20px;
  left:30px;
}
.card:hover .card-line{
  border-top: 10px dotted #ebe0e0 !important
}
.delete-btn:focus {
  background-color:  #ebe0e0 ;
}
